.cfger-block {
  margin-bottom: 10px;
}

.cfger-root-button {
  position: relative;
  top: -39px;
  float: right;
  margin-bottom: -31px;
  right: 20px;
}

.cfger-container {
  padding: 5px;
  background-color: #EDEDED;
  border: 1px solid #DFDFDF;
}

.cfger-option {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  padding-left: 2px;
}

.cfger-header {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  border-bottom: 1px solid #cecece;
  padding-bottom: 4px;
  cursor: pointer;
}

.cfger-button {
  display: inline-block;
  font-weight: 400;
  line-height: 1.5;
  color: #5a5c69;
  text-align: center;
  text-decoration: none;
  vertical-align: middle;
  cursor: pointer;
  -webkit-user-select: none;
  user-select: none;
  background-color: transparent;
  border: 1px solid transparent;
  transition: color 0.15s ease-in-out, background-color 0.15s ease-in-out, border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
  padding: 0.25rem 0.5rem;
  font-size: 0.875rem;
  border-radius: 0.2rem;
}

.cfger-button:hover {
  background-color: #e3e3e3;
}

.cfger-label {
  text-align: right;
  margin-right: 10px;
}

.cfger-disabled {
  opacity: 0.5;
  pointer-events: none;
  user-select: none;
}

.cfger-empty-list-label {
  color: #c3c3c3;
}

.cfger-divider-block {
  padding: 15px 0;
  margin: 35px 0px 10px;
  border-top: 1px solid #e7e7e7;
}
.cfger-divider-block label {
  font-weight: 700;
}

.file-input {
  display: none;
}

.file-btn {
  margin: 0 2px;
  z-index: 5;
  background-color: #EDEDED;
  cursor: pointer;
  margin-bottom: 0px;
  border-radius: 0.35rem;
  padding: 0.375rem 0.75rem;
  border: 1px solid #ced4da;
  font-size: 1rem;
  font-weight: 400;
}
.file-btn .file-icon {
  font-size: 1.35rem;
  margin: 0 auto;
  vertical-align: middle;
}
.file-btn span {
  display: inline-block;
  margin: 0.375rem 0.75rem;
  margin-left: 0.4rem;
}

.file-btn.disabled {
  opacity: 0.5;
}

.file-btn.active {
  background-color: white;
  color: black;
}

.file-btn:hover:not(:disabled) {
  opacity: 0.8;
  filter: brightness(0.7);
}

.custom-file {
  display: flex;
}

.file-preview-block {
  position: absolute;
  display: none;
  background-color: white;
  background-image: linear-gradient(45deg, #b0b0b0 25%, transparent 25%), linear-gradient(-45deg, #b0b0b0 25%, transparent 25%), linear-gradient(45deg, transparent 75%, #b0b0b0 75%), linear-gradient(-45deg, transparent 75%, #b0b0b0 75%);
  background-size: 10px 10px;
  background-position: 0 0, 0 5px, 5px -5px, -5px 0px;
  border: 3px solid #ced4da;
  padding: 0.5rem;
  border-radius: 0.35rem;
  transform: translate(-100%, 0);
  z-index: 100;
}

.file-preview-content {
  max-width: 30vmin;
  min-width: 150px;
}

.file-drowdown-btn.active {
  border-radius: 0.35rem 0.35rem 0 0;
  border: 1px solid #ced4da;
  border-bottom: none;
}

.block-popover-container {
  position: relative;
  width: 0;
  height: 0;
}

.file-config-block.active {
  display: block;
}

.file-config-block {
  z-index: 10;
  position: absolute;
  display: none;
  background-color: white;
  padding: 1rem;
  border-radius: 0.35rem;
  border: 1px solid #ced4da;
  border-radius: 0.35rem 0 0.35rem 0.35rem;
  transform: translate(calc(-100% + 44px), -3px);
}
.file-config-block .image-file-size {
  font-size: 12px;
  font-weight: bold;
  text-align: end;
  margin-top: -7px;
  margin-bottom: 7px;
}
.file-config-block .btn-block {
  display: flex;
  justify-content: space-between;
  margin-bottom: -5px;
  margin-right: -5px;
  margin-left: -5px;
}
.file-config-block .btn-block .btn {
  display: inline-block;
  font-weight: 400;
  line-height: 1.5;
  color: #5a5c69;
  text-align: center;
  text-decoration: none;
  vertical-align: middle;
  cursor: pointer;
  -webkit-user-select: none;
  user-select: none;
  background-color: transparent;
  border: 1px solid transparent;
  padding: 0.25rem 0.55rem;
  font-size: 1rem;
  border-radius: 0.35rem;
  color: white;
}
.file-config-block .btn-block .btn:hover {
  opacity: 0.8;
  filter: brightness(0.9);
}
.file-config-block .btn-block .btn.apply {
  background-color: #1abc9c;
}
.file-config-block .btn-block .btn.reset {
  background-color: #1e90ff;
}

.config-drowdown-btn {
  z-index: 5;
  cursor: pointer;
  opacity: 1;
}

.config-drowdown-btn:hover {
  opacity: 0.6;
}

.config-popover-container {
  position: relative;
  width: 0;
  height: 0;
}

.config-popover-block {
  flex-direction: column;
  border: 1px solid #dddddd;
  border-radius: 0.35rem;
  padding: 0.375rem 0.75rem;
  width: 200px;
  background-color: #fff;
  z-index: 10;
  position: absolute;
  display: none;
  transform: translate(calc(-100% + 41px), 5px);
}
.config-popover-block .popover-label {
  font-size: 12px;
  font-weight: 800;
  color: #a6a6a6;
}

.config-popover-block.active {
  display: block;
}

.expanded-select-body {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  flex-direction: row;
  padding: 0.5rem;
  overflow-x: hidden;
  overflow-y: auto;
  max-height: 25vh;
}

.expanded-select {
  position: absolute;
  display: none;
  background-color: white;
  padding: 0.5rem;
  border-radius: 0.35rem;
  border: 1px solid #ced4da;
  z-index: 10;
  width: 28vw;
  overflow: hidden;
  transform: translate(-100%, -50%);
}

.expanded-select-header {
  display: flex;
  justify-content: space-between;
  line-height: 33px;
  margin-bottom: 0.25rem;
}

.expanded-select-title {
  font-weight: bold;
  font-size: 1.5rem;
  margin-bottom: 0;
}

.expanded-select-close-btn {
  border: none;
  background-color: white;
  font-size: 3rem;
  font-weight: bold;
  transform: scale(1);
}

.expanded-select-close-btn:hover {
  transform: scale(0.9);
}

.expanded-select.active {
  display: block;
}

.select-preview {
  margin: 5px;
  transform: scale(1);
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  -webkit-transition: transform 0.15s ease-out;
  -moz-transition: transform 0.15s ease-out;
  -o-transition: transform 0.15s ease-out;
  transition: transform 0.15s ease-out;
}

.select-preview.selected {
  background-color: #f7f7f7;
  border-radius: 9px;
  border: 1px solid #d4d4d4;
}

.select-preview:hover {
  transform: scale(0.9);
}

.block-popover-backdrop.active {
  display: block;
}

.block-popover-backdrop {
  display: none;
  position: absolute;
  width: 100vw;
  height: 100vh;
  transform: translate(-100%, 0);
  top: -0.5rem;
  left: calc(100% + 0.5rem);
  overflow: hidden;
}

.cfger-indicator {
  margin: 0;
  border: 1px solid #c5c5c5;
  padding: 0px 3px;
  background-color: #ffffff;
  font-family: monospace;
  -moz-appearance: textfield;
  margin-left: 7px;
}

.cfger-indicator::-webkit-outer-spin-button,
.cfger-indicator::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

.cfger-indicator:focus {
  border-color: rgba(153, 221, 245, 0.84);
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.075) inset, 0 0 8px rgba(104, 202, 239, 0.6);
  outline: 0 none;
}

input.custom-range {
  position: relative;
  --label-max: "";
  --label-min: "";
}
input.custom-range:before {
  content: var(--label-min);
  position: absolute;
  left: -3px;
  top: -1px;
  font-size: 13px;
  font-weight: 800;
  display: block;
  transform: translate(-100%);
}
input.custom-range:after {
  display: block;
  font-size: 13px;
  font-weight: 800;
  position: absolute;
  content: var(--label-max);
  transform: translate(100%);
  right: -3px;
  top: -1px;
}

.font-row {
  display: flex;
  flex-wrap: nowrap;
  margin-bottom: 8px;
  align-items: flex-end;
}
.font-row select.form-select {
  border: 0;
  font-size: 15px;
  font-weight: 800;
  padding-bottom: 0;
}
.font-row select.form-select:focus {
  box-shadow: none;
}
.font-row:last-of-type {
  margin-bottom: 0;
}
.font-row .font-label {
  font-size: 15px;
  font-weight: 800;
  color: #a6a6a6;
  margin-right: 5px;
}
.font-row .font-value {
  font-size: 15px;
  font-weight: 800;
  width: 100%;
  min-width: var(--max-value-length);
}
.font-row .font-color-container {
  padding: 0;
  border: 0;
}
.font-row .font-range-btn {
  display: flex;
  flex-wrap: nowrap;
}
.font-row .font-range-icon {
  margin-left: 5px;
}
.color-alpha-input-container {
  display: flex;
  padding: 0.375rem 0.75rem;
  border-radius: 0.35rem;
  background-color: #fff;
  border: 1px solid #dddddd;
  align-items: center;
}
.color-alpha-input-container .color-alpha-input-color:focus {
  box-shadow: none;
}
.color-alpha-input-container .color-alpha-input-color-border {
  border: 1px solid #dddddd;
  background-color: white;
  background-size: 10px 10px;
  background-position: 0 0, 0 5px, 5px -5px, -5px 0px;
  background-image: linear-gradient(45deg, #b0b0b0 25%, transparent 25%), linear-gradient(-45deg, #b0b0b0 25%, transparent 25%), linear-gradient(45deg, transparent 75%, #b0b0b0 75%), linear-gradient(-45deg, transparent 75%, #b0b0b0 75%);
}
.color-alpha-input-container .color-alpha-input-color {
  padding: 0;
  width: 50px;
  height: 27px;
  border: 0;
}
.color-alpha-input-container .color-alpha-btn {
  margin-left: 5px;
  padding: 0 0.15rem;
}
.color-alpha-input-container .alpha-input-block.active {
  display: flex !important;
}
.color-alpha-input-container .alpha-input-block {
  display: none;
  flex-direction: column;
  border: 1px solid #dddddd;
  border-radius: 0.35rem;
  padding: 0.375rem 0.75rem;
  width: 200px;
  background-color: #fff;
}
.color-alpha-input-container .alpha-input-block label {
  font-size: 12px;
  font-weight: 800;
  color: #a6a6a6;
}
.color-alpha-input-container .alpha-input-block input {
  position: relative;
}
.color-alpha-input-container .alpha-input-block input.custom-range::before {
  content: "0";
  position: absolute;
  left: -9px;
  top: -1px;
  font-size: 13px;
  font-weight: 800;
}
.color-alpha-input-container .alpha-input-block input.custom-range::after {
  font-size: 13px;
  font-weight: 800;
  position: absolute;
  content: "1";
  right: -9px;
  top: -1px;
}

.custom-input-container {
  display: flex;
  flex-direction: column;
  padding: 0.375rem 0.75rem;
  border-radius: 0.35rem;
  background-color: #fff;
  border: 1px solid #dddddd;
}

.font-size-icon {
  background-image: url("./icons/font-size.png");
  background-repeat: no-repeat;
  background-size: contain;
  display: inline-block;
  height: 20px;
  vertical-align: middle;
  width: 20px;
}